@import "~@blueprintjs/core/lib/css/blueprint.css";
@import "~@blueprintjs/core/lib/scss/variables";

$bad_cost_background: rgb(239, 225, 229);
$bad_cost_text: $vermilion3;
$good_cost_background: rgb(225, 239, 229);
$good_cost_text: $forest2;
$neutral_background: white;

th {
  background-color: $light-gray5;
}

td p,
th p {
  margin: 0;
  font-weight: 500;
}

td p,
th.checkin-date p {
  text-align: center;
}

td {
  &.clickable {
    cursor: pointer;

    &.cost-1 {
      background-color: $bad_cost_background;
      color: $bad_cost_text;
      &.active:not(:active) {
        background-color: darken($bad_cost_background, 10%);
      }
      &:hover {
        background-color: darken($bad_cost_background, 4%);
      }
      &:active {
        background-color: darken($bad_cost_background, 15%);
      }
      &.tint {
        background-color: darken($bad_cost_background, 5%);
      }
    }

    &.cost-2 {
      color: $bad_cost_text;
      background-color: $white;
      &.tint {
        background-color: darken($white, 5%);
      }
    }

    &.cost-3 {
      background-color: $white;
      &.tint {
        background-color: darken($white, 5%);
      }
    }

    &.cost-4 {
      color: $good_cost_text;
      background-color: $white;
      &.tint {
        background-color: darken($white, 5%);
      }
    }

    &.cost-5 {
      background-color: $good_cost_background;
      color: $good_cost_text;
      &.active:not(:active) {
        background-color: darken($good_cost_background, 10%);
      }
      &:hover {
        background-color: darken($good_cost_background, 4%);
      }
      &:active {
        background-color: darken($good_cost_background, 15%);
      }
      &.tint {
        background-color: darken($good_cost_background, 5%);
      }
    }
  }

  &.cost-2,
  &.cost-3,
  &.cost-4,
  & {
    &.active:not(:active) {
      background-color: darken($white, 10%);
    }
    &:hover {
      background-color: darken($white, 4%);
    }
    &:active {
      background-color: darken($white, 15%);
    }
    &.tint {
      background-color: darken($white, 5%);
    }
  }
}
