@import "~@blueprintjs/core/lib/css/blueprint.css";
@import "~@blueprintjs/core/lib/scss/variables";

.popover {
  .#{$ns}-menu {
    max-height: 400px;
    overflow: auto;
  }
  .#{$ns}-input {
    width: 80vw;
    min-height: 40px;
  }
  .#{$ns}-input-ghost {
    font-size: 14px;
  }
}

.table-container {
  display: flex;
  flex-direction: column;
}

.filters {
  display: flex;
  flex-direction: column;
}

.no-wrap {
  white-space: nowrap;
}

.container {
  display: flex;
  flex-direction: row;
  height: 100vh;
}

.sidebar {
  width: 400px;
  background-color: $light-gray4;
  display: flex;
}

.hotels {
  overflow-y: scroll;
  padding: 0 20px;
}

.content {
  flex-grow: 1;
  height: 100vh;
  padding: 20px;
  overflow-y: scroll;
}
